
import { defineComponent } from 'vue'
import { Event } from '@/types/todomvc'

export default defineComponent({
  name: 'TodoHeader',
  setup (props, { emit }) {
    const handleNewTodo = (e: Event) => {
      const value = e.target.value.trim()
      if (!value.length) return
      emit('new-todo', value)
      e.target.value = ''
    }

    return {
      handleNewTodo
    }
  }
})
