
import { computed, defineComponent, ref } from 'vue'
import { Event } from '@/types/todomvc'

export default defineComponent({
  name: 'TodoItem',
  props: {
    todo: {
      type: Object,
      default: () => ({})
    }
  },
  directives: {
    focus (element, binding) {
      if (binding.value) {
        element.focus()
      }
    }
  },
  setup (props, { emit }) {
    const isEditing = ref(false)
    const item = computed({
      get: () => {
        return props.todo
      },
      set: () => ({})
    })
    const handleEditTodo = (e: Event) => {
      emit('edit-todo', {
        id: item.value.id,
        text: e.target.value
      })
      // 取消编辑状态
      isEditing.value = false
    }

    const handleCancelEdit = () => {
      isEditing.value = false
    }

    return {
      isEditing,
      item,
      handleEditTodo,
      handleCancelEdit
    }
  }
})
